div.Header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-right: 2em;
  padding-left: 2em;
  background-color: #F4F4F4;
}

div.Header > div.header-logo-container > img.logo {
  width: 60px;
}

div.Header > div.header-options-container {
  margin-left: auto;
}

div.Header > div.header-options-container > ul.header-option-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  list-style: none;
}

div.Header > div.header-options-container > ul.header-option-list > li.header-list {
  margin-left: 1.2em;
}

div.Header > div.header-options-container > ul.header-option-list > li.header-list:hover {
  color: gray;
  cursor: pointer;
}


